.header {
  min-height: 100px;
  padding: 25px 0px 0px 20px;
  background-color:  rgb(20, 20, 104);
  color: white;
}

.header h1 {
  font-size: 30px;
}

.gameMsg{
  color: orange;
  padding: 10px;
}

.logoImg{
  height: 60px;
}

.imgRnd10 {
  /* Rounded image corners */
  border-radius: 10px;
}

.imageShadow {
  /* Add drop shadow */
  box-shadow: 2px 2px 3px 2px rgb(245, 230, 19);
}

.imageRotate {
  /* Rotate Image */
  -webkit-transform: rotate( 3deg);
  transform: rotate( 3deg);
}
.jumbotron {
  min-height: 250px;
  padding-top: 80px;
  background-color: black;
  color: white;
  /* Transparent background */
  opacity: 0.8;
}

@media screen and (max-width: 985px) and (min-width: 767px) {
  .jumbotron {
    padding-top: 30px;
    padding-bottom: 10px;
    height: 20px;
    background-color: blue;
  }
}

@media screen and (max-width: 767px) {
  .jumbotron {
    display: none;
  }
}
.footer {
  min-height: 70px;
  padding: 25px 0px 0px 20px;
  background-color: rgb(20, 20, 104);
  color: white;
}

.footerImg {
  width: 20px;
}
.card {
  margin: 30px 25px 0px 25px;
  border: 0px;
}

.imgHolder {
  background-color: #DBE9F4; 
  padding: 0px 0px 0px 0px;
}

.characterImg {
  height: 120px;
  margin-top: 0px;
  margin-left: 0px;
}

.imgBtn {
  border: 4px solid rgb(126, 183, 206);
}

.imgRnd10 {
  /* Rounded image corners */
  border-radius: 10px;
}

@media screen and (max-width: 985px) and (min-width: 767px) {
  .characterImg {
    height: 90px;
    margin-top: 0px;
    margin-left: 0px;
  }
}

@media screen and (max-width: 767px) {
  .characterImg {
    height: 150px;
      margin-top: 0px;
      margin-left: 0px;
  }
}

.gameRow {
  margin: 0px 40px 0px 40px;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

.container {
  background-color: #DBE9F4; 
  /* Transparent background */
  opacity: 0.9;
  margin-top: 10px;
  margin-bottom: 20px;
  /* Set min height to fill larger screens */
  min-height: 500px;
}

@media screen and (max-width: 985px) and (min-width: 767px) {
  .container {
      min-height: 420px;
  }
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

